.map {
    width: fit-content;
    position: relative;

    div {
        background-color: #fff;
        width: 5px;
        height: 5px;
        background-color: #f9b871;
        border-radius: 50%;
        box-shadow: 0px 0px 9px 4px #f9b871;
        animation: pulse 1.5s linear infinite alternate;
        position: absolute;
    }

    &-canada {
        top: 17%;
        left: 17%;
    }
    &-usa {
        top: 30%;
        left: 17%;
    }
    &-russia {
        top: 24%;
        right: 28%;
    }
    &-india {
        top: 43%;
        right: 28.7%;
    }
    &-ksa {
        top: 39%;
        right: 39.5%;
    }
    &-egypt {
        top: 38%;
        left: 55%;
    }
    &-italy {
        top: 28%;
        left: 50.8%;
    }
    &-norway {
        top: 13%;
        left: 49.8%;
    }
    &-ukraine {
        top: 20%;
        left: 53%;
    }
    &-poland {
        top: 21%;
        left: 49%;
    }
    &-uk {
        top: 17.5%;
        left: 45%;
    }
    &-australia {
        bottom: 23%;
        right: 13%;
    }
    &-malaysia {
        bottom: 41.5%;
        right: 17%;
    }
    &-taiwan {
        top: 40%;
        right: 15.5%;
    }
}

@keyframes pulse {
    to {
        box-shadow: 0px 0px 27px 17px #f9b871;
    }
}

@media (max-width: 567px) {
    @keyframes pulse {
        to {
            box-shadow: 0px 0px 15px 8px #f9b871;
        }
    }
}